.container {
  max-width: 800px; /* Adjust the max-width as needed */
  margin: 0 auto; /* Center the container horizontally */
  text-align: center; /* Center align content */
}

.input-group {
  margin-bottom: 20px; /* Add some spacing below the input group */
}

.input-group-append {
  width: 80%; /* Set the width of the input group append to 80% */
  margin-left: auto; /* Align to the right within the parent container */
  margin-right: auto; /* Align to the left within the parent container */
}

.btn-primary {
  margin-top: 20px;
  width: 80%; /* Set the width of the button to 80% */
}

.form-control{
  width:80%;
  text-align: center;

}

.btn-primary-1 {
  background-color: #EA4C89;
  border-radius: 8px;
  border-style: none;
  box-sizing: border-box;
  color: #FFFFFF;
  cursor: pointer;
  display: inline-block;
  font-family: "Haas Grot Text R Web", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 14px;
  font-weight: 500;
  height: 40px;
  line-height: 20px;
  list-style: none;
  margin: 0;
  outline: none;
  padding: 10px 16px;
  position: relative;
  text-align: center;
  text-decoration: none;
  transition: color 100ms;
  vertical-align: baseline;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.button-1:hover,
.button-1:focus {
  background-color: #F082AC;
}