/* src/components/Cards.css */

.card-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .card {
    position: relative;
    width: 300px;
    background-color: #f8f9fa;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    margin: 10px;
    display: flex;
    flex-direction: column;
    transition: transform 0.3s; /* Smooth transition for card zoom */
  }
  
  .card:hover {
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
    transform: scale(1.05); /* Slight zoom effect on hover */
  }
  
  .card-img-top {
    width: 100%;
    height: 200px;
    object-fit: cover;
    transition: transform 0.3s; /* Smooth transition for image zoom */
  }
  
  .card:hover .card-img-top {
    transform: scale(1.1); /* Zoom effect for the image */
  }
  
  .card-body {
    padding: 10px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center; /* Center items horizontally */
  }
  
  .card-title {
    margin: 0;
    font-size: 16px;
    font-weight: bold;
  }
  
  .card-text {
    margin: 5px 0;
    font-size: 14px;
  }
  
  .btn-primary {
    display: block;
    margin-top: 10px;
    color: #fff;
    background-color: #007bff;
    border: none;
    border-radius: 4px;
    padding: 6px 12px;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
  }
  
  .btn-primary:hover {
    background-color: #0056b3;
  }
